import _ from 'lodash';
import React, { useState } from 'react';
import { makeStyles, Grid, Dialog, DialogProps, useTheme, useMediaQuery } from '@material-ui/core';
import Typography from '../components/Typography';
import FlexBox from '../components/FlexBox';
import ImageCard, { ImageCardProps } from '../components/ImageCard';
import { ReactComponent as LongArrowRight } from '../media/longArrowRight.svg';
import NavLeftRight from '../components/NavLeftRight';
import { PageRoutes } from '../models/routes';
import classNames from 'classnames';
import SwipeableTextMobileStepper from '../components/SwipeableTextMobileStepper';

const useStyles = makeStyles((theme) => ({
  portfolioContentContainer: {
    flexDirection: 'column',
    padding: theme.spacing(4),
  },
  portfolioSubPage: {
    flexDirection: 'column',
    '&:not(:first-child)': {
      marginTop: theme.spacing(6),
    },
  },
  portfolioTitleContainer: {
    alignItems: 'baseline',
    marginBottom: theme.spacing(2),
    [theme.breakpoints.down('xs')]: {
      flexDirection: 'column',
    },
  },
  subtitle: {
    fontWeight: 200,
    whiteSpace: 'pre',
  },
  gridItem: {
    alignItems: 'center',
    display: 'flex',
    '& > .MuiCard-root': {
      '&:hover $imageOverlay': {
        background: 'rgb(0 0 0 / 80%)',
        opacity: 1,
      },
    },
  },
  imageOverlay: {
    padding: theme.spacing(2),
    opacity: 0,
    position: 'absolute',
    bottom: 0,
    right: 0,
    transition: 'opacity 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
  },
  preWhiteSpace: {
    whiteSpace: 'pre',
  },
  separator: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  viewLabel: {
    color: 'white',
    fontWeight: 'bold',
    marginRight: theme.spacing(1),
  },
  viewArrow: {
    fill: 'white',
    height: '1.2em'
  },
}));

type PortfolioCardProps = {
  image: string,
  onClick: ImageCardProps['onClick'],
  delay?: number
}
const PortfolioCard = ({ image, onClick, delay = 0 }: PortfolioCardProps) => {
  const classes = useStyles();
  return (
    <Grid item xs={12} sm={6} md={4} xl={1} className={classes.gridItem}>
      <ImageCard image={image} onClick={onClick} data-aos-delay={delay}>
        <div className={classes.imageOverlay}>
          <FlexBox alignItems='center'>
            <Typography header variant='h5' className={classes.viewLabel}>
              View
            </Typography>
            <LongArrowRight className={classes.viewArrow} />
          </FlexBox>
        </div>
      </ImageCard>
    </Grid>
  );
};

const importAllImages = (r: __WebpackModuleApi.RequireContext) => {
  return r.keys().map(r) as string[];
};

const PORTFOLIO_IMAGES = _.mapValues({
  livingRoom: importAllImages(require.context('../media/portfolio/LivingRoom', false, /\.(png|jpe?g|svg)$/)),
  diningRoom: importAllImages(require.context('../media/portfolio/DiningRoom', false, /\.(png|jpe?g|svg)$/)),
  guestBedroom: importAllImages(require.context('../media/portfolio/GuestBedroom', false, /\.(png|jpe?g|svg)$/)),
  bedroom: importAllImages(require.context('../media/portfolio/Bedroom', false, /\.(png|jpe?g|svg)$/)),
  kitchen: importAllImages(require.context('../media/portfolio/Kitchen', false, /\.(png|jpe?g|svg)$/)),
  patio: importAllImages(require.context('../media/portfolio/Patio', false, /\.(png|jpe?g|svg)$/)),
  threeD: importAllImages(require.context('../media/portfolio/3D', false, /\.(png|jpe?g|svg)$/)),
}, (value) => value.map(image => ({ image })));

type RoomKey = keyof (typeof PORTFOLIO_IMAGES);

const PORTFOLIO_ORDER = [
  'livingRoom',
  'diningRoom',
  'guestBedroom',
  'bedroom',
  'kitchen',
  'patio',
] as RoomKey[];

type PortfolioCarouselProps = {
  roomKey: RoomKey | null;
  onClose: DialogProps['onClose'];
}
const PortfolioCarousel = ({ roomKey, onClose }: PortfolioCarouselProps) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  return (
    <Dialog open={!!roomKey} onClose={onClose} fullScreen={fullScreen} maxWidth={false}>
      {roomKey && <SwipeableTextMobileStepper steps={PORTFOLIO_IMAGES[roomKey]} />}
    </Dialog>
  )
};

const PortfolioPage = () => {
  const classes = useStyles();
  const [roomKey, setRoomKey] = useState<RoomKey | null>(null);
  return (
    <FlexBox className={classes.portfolioContentContainer}>
      <FlexBox className={classes.portfolioSubPage}>
        <FlexBox className={classes.portfolioTitleContainer} data-aos="fade-up">
          <Typography header variant='h4' className={classes.preWhiteSpace}>
            Portfolio
          </Typography>
          <Typography className={classNames(classes.subtitle, classes.separator)} variant='h4'>
            {` | `}
          </Typography>
          <Typography className={classes.subtitle} variant='h4'>
            {`Home Staging `}
          </Typography>
          <Typography className={classes.subtitle} variant='h4'>
            {`+ Interior Design`}
          </Typography>
        </FlexBox>
        <Grid container spacing={4}>
          {PORTFOLIO_ORDER.map((key, i) => (
            <PortfolioCard key={`${key}-${i}`} image={PORTFOLIO_IMAGES[key][0].image} data-aos='fade-right' delay={i * 250} onClick={() => setRoomKey(key)} />
          ))}
        </Grid>
        <PortfolioCarousel roomKey={roomKey} onClose={() => setRoomKey(null)} />
      </FlexBox>
      <FlexBox className={classes.portfolioSubPage}>
        <FlexBox marginBottom={2}>
          <Typography header variant='h4'>
            Portfolio
          </Typography>
          <Typography className={classes.subtitle} variant='h4'>
            {` | 3D`}
          </Typography>
        </FlexBox>
        <Grid container spacing={4}>
          <Grid item xs={12} sm={6} className={classes.gridItem}>
            <ImageCard image={PORTFOLIO_IMAGES.threeD[0].image} />
          </Grid>
          <Grid item xs={12} sm={6} className={classes.gridItem}>
            <ImageCard image={PORTFOLIO_IMAGES.threeD[1].image} />
          </Grid>
        </Grid>
      </FlexBox>
      <NavLeftRight
        left={{ label: 'View Services', href: PageRoutes.Services }}
        right={{ label: 'Contact Us', href: PageRoutes.Contact }}
        data-aos='fade-down'
      />
    </FlexBox>
  );
};

export default PortfolioPage;